import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import styled from 'styled-components';
import videoSrc from '../assets/videos/hero.mp4';

const Wrapper = styled.div`
    position: relative;
    width: 100vw;
    min-height: ${props => props.isBig ? '100vh' : '75vh'};

    height: ${props => props.isVideo ? '100vh' : 'none'};
    overflow: hidden;

    .video-wrapper {
        height: 100%;
        top: 0;
        left: 0;
        overflow: hidden;
        position: absolute;
        width: 100%;
        z-index: 0;

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .content {
        background: rgba(17, 17, 17, 0.3);
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &:before {
        background: url(${props => props.bgImage});
        
        ${props => !props.isVideo && `
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-attachment: fixed;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            filter: brightness(60%);
        `}

        @media screen and (max-width: 991px) {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-attachment: fixed;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            filter: brightness(60%);
        }
    }
    
    .chevron-down {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%);        
        cursor: pointer;
    }
`

const Hero = ({ isBig, img, title, subtitle, children, isVideo }) => {

    const videoRef = useRef(null);

    useEffect(() => {

        if (videoRef && videoRef.current) {
            videoRef.current.play();
        }
    }, [videoRef]);


    return (
        <>
            <header>
                <Wrapper bgImage={img} isBig={isBig} isVideo={isVideo} className={classNames("header-wrapper")}>
                    {isBig && (
                        <>
                            <div className="video-wrapper">
                                <video
                                    ref={videoRef}
                                    controls={false}
                                    muted={true}
                                    autoPlay={true}
                                    loop={true}
                                    width="100%"
                                >
                                    <source src={videoSrc} type="video/mp4"></source>
                                </video>
                            </div>
                        </>
                    )}
                    <div className="content">
                        <div>
                            <h1>{title}</h1>
                            <hr className="my-4 white" />
                            {subtitle && (<p className="text-heading">{subtitle}</p>)}

                            <div>
                                {children}
                            </div>

                            {isBig && (
                                <a href="#chevron" className="chevron-down" id="chevron">
                                    <svg width="2em" height="2em" viewBox="0 0 16 16" class="bi bi-chevron-down" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                        <path fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z" />
                                    </svg>
                                </a>
                            )}
                        </div>
                    </div>
                </Wrapper>
            </header>
        </>
    )
}

Hero.defaultProps = {
    isBig: false,
    img: "",
    title: "",
    subtitle: "",
}

Hero.propTypes = {
    isBig: PropTypes.bool,
    img: PropTypes.string.isRequired,
    alt: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string.isRequired,
}

export default Hero;